<template>
	<div class="articlesList diyscrollbar">
		<div class="article" v-for="(a,index) in articles">
			<el-badge v-bind:value="a.state==0?'未发表':''" class="item">
				<div class="a-title" :title="a.title">{{a.title}}</div>
			</el-badge>
			<!--<div class="a-author">
				作者&nbsp;:&nbsp;{{a.author}}
			</div>-->
			<div class="a-time">
				发布时间&nbsp;:&nbsp;{{a.publishedTime}}
			</div>
			<!--<div class="a-views">
				浏览次数&nbsp;:&nbsp;{{a.views}}
			</div>-->
			<div class="a-content">
				内容摘要&nbsp;:&nbsp;{{getAbstracts(a.mdContent)}}
			</div>
			<div class="a-tags">
				文章标签&nbsp;:&nbsp;<br />
				<span v-for="tag in a.tagSet">
					{{tag.name}}
				</span>
			</div>
			<div class="a-operation">
				<el-button v-on:click="read(a)" type="info" icon="view"></el-button>
				<el-button v-on:click="amend(a)" type="primary" icon="edit"></el-button>
				<el-button v-on:click="delArticle(a)" type="danger" icon="delete"></el-button>
			</div>
		</div>
		<div class="fixclear"></div>
		<div class="paging">
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page.currentPage" :page-sizes="[20, 60, 80, 100,500,1000]" :page-size="20" layout="total, sizes, prev, pager, next, jumper" :total="page.totalArticles">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { Badge } from 'element-ui'
	import { markdown } from 'markdown'
	import Vue from 'vue'
	export default {
		name: 'articlesList',
		props: ['articles','page'],
		components: {
			'el-badge': Badge,
		},
		data() {
			return {

			}
		},
		created() {

		},
		methods: {
			getAbstracts(mdContent) {//获取文章前面的200个子非鱼并过滤
				return markdown.toHTML(mdContent).replace(/<[^>]+>/g, "").substring(0, 200);
			},
			handleSizeChange(val) {
				//console.log(`每页 ${val} 条`);
				this.$emit('setCurrentNum', val);
				this.$emit('childGetArticles', this.page.currentPage, val, this.page.searchText);
			},
			handleCurrentChange(val) {
				//this.currentPage = val;
				//console.log(`当前页: ${val}`);
				this.$emit('setCurrentPage', val);
				this.$emit('childGetArticles', val, this.page.currentNum, this.page.searchText);
			},
			delArticle(a) {
				this.$emit('del', a);
			},
			read(a) {
				this.$emit('read', a);
			},
			amend(a) {
				this.$emit('amend', a);
			}
		},
	}
</script>

<style>
	.articlesList {
		position: relative;
		padding: 5px 10px;
		overflow: auto;
		height: calc(100% - 66px);
	}
	
	.article {
		float: left;
		position: relative;
		width: 250px;
		margin: 7px;
		border: 1px solid #ccc;
	}
	
	.a-title {
		font-size: 20px;
		padding: 7px;
		width: 236px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		border-bottom: 1px solid #ccc;
	}
	
	.a-author,
	.a-time,
	.a-views,
	.a-content,
	.a-tags {
		font-size: 13px;
		line-height: 20px;
		padding: 1px 5px;
	}
	
	.article .el-badge__content.is-fixed {
		z-index: 3;
		right: 38px;
	}
	
	.a-content {
		display: -webkit-box !important;
		overflow: hidden;
		text-overflow: ellipsis;
		word-break: break-all;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
	}
	
	.a-tags {
		background-color: #fff;
	}
	
	.a-tags span {
		display: inline-block;
		font-size: 13px;
		border: 1px solid #20A0FF;
		padding: 2px 10px;
		margin: 1px 0;
		border-radius: 40px;
		cursor: pointer;
		color: #1D8CE0;
		max-width: 218px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	
	.a-operation {
		padding: 5px;
	}
	
	.a-operation .el-button {
		margin-left: 5px;
		padding: 7px;
	}
</style>


